.switch-label {
  width: 100%;
  height: 50px;
  background-color: var(--dark);
  cursor: pointer;
  border: 3px solid var(--dark);
  border-radius: 25px;
  position: absolute;
}

.checkbox {
  display: none;
  position: absolute;
}

.slider {
  width: 100%;
  height: 100%;
  border-radius: 25px;
  transition: all .3s;
  position: absolute;
}

.checkbox:checked ~ .slider {
  background-color: var(--light);
}

.slider:before {
  content: "";
  width: 25px;
  height: 25px;
  -webkit-box-shadow: inset 12px -4px 0px 0px var(--light);
  box-shadow: inset 12px -4px 0px 0px var(--light);
  background-color: var(--dark);
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 10px;
  left: 10px;
}

.checkbox:checked ~ .slider:before {
  background-color: var(--dark);
  -webkit-box-shadow: none;
  box-shadow: none;
  transform: translateX(50px);
}

